// src/components/PosterDisplay.js
import React from "react";
import { useNavigate } from "react-router-dom";
import "../styles/PosterDisplay.css";

// --- UPDATED IMAGE PATH ---
// Construct the path using PUBLIC_URL to handle base paths correctly
const posterImagePath = `${process.env.PUBLIC_URL}/poster.png`;
// --- END UPDATE ---

function PosterDisplay() {
  const navigate = useNavigate();

  function handleBackClick() {
    navigate(-1); // Go back to the previous page in history
  }

  return (
    <div className="poster-display-container">
      <div className="poster-controls">
        <button onClick={handleBackClick} className="back-button">
           Back
        </button>
      </div>
      <div className="poster-image-wrapper">
        <img
          src={posterImagePath} // Use the corrected path
          alt="Research Poster"
          className="poster-image"
          // Add error handler for debugging
          onError={(e) => console.error("Image failed to load:", e.target.src)}
        />
      </div>
    </div>
  );
}

export default PosterDisplay;
