import React, { useState } from "react";
import "../styles/DefinitionSelector.css";

/**
 * Global component for selecting and switching between prime definitions
 * This appears consistently across all dashboard tabs
 *
 * @param {Object} props
 * @param {Array} props.availableDefinitions - List of all available definitions
 * @param {string} props.activeDefinitionKey - Key of the currently active definition
 * @param {Function} props.onDefinitionChange - Callback when definition is changed
 * @param {Boolean} props.isModified - If the current view is using modified data
 * @param {Function} props.getColorForDefinition - Function to get color for definition ID
 */
const DefinitionSelector = ({
  availableDefinitions = [],
  activeDefinitionKey,
  onDefinitionChange,
  isModified = false, // This prop might be redundant now if logic relies on activeDefinitionKey !== 'original'
  getColorForDefinition, // Added prop for consistency
}) => {
  const [isExpanded, setIsExpanded] = useState(false);

  // Do not render if there are no alternate definitions (or only original)
  if (!availableDefinitions || availableDefinitions.length <= 1) {
    return null;
  }

  const handleDefinitionSelect = (definitionKey) => {
    if (definitionKey !== activeDefinitionKey) {
      onDefinitionChange(definitionKey);
    }
    setIsExpanded(false); // Close dropdown after selection
  };

  // Find active definition object
  const activeDefinition = availableDefinitions.find(
    (def) => def.id === activeDefinitionKey
  );

  // Determine label and color, providing fallbacks
  const activeLabel = activeDefinition?.label ?? "Original Data";
  const isOriginal = activeDefinitionKey === "original_data_definition";
  const defaultColor = isOriginal ? "#006064" : "#ff8f00"; // Default fallback colors

  // Use getColorForDefinition if provided, otherwise use object's color or fallback
  const definitionColor = getColorForDefinition
    ? getColorForDefinition(activeDefinitionKey)
    : activeDefinition?.color || defaultColor;

  return (
    <div className="definition-selector-container">
      <div
        className={`definition-selector-toggle ${
          isExpanded ? "expanded" : ""
        } ${isOriginal ? "original" : "custom"}`}
        onClick={() => setIsExpanded(!isExpanded)}
        style={{
          borderLeftColor: definitionColor,
          // Optional: Set text color to match border, or keep it default
          // color: definitionColor,
        }}
      >
        <div className="active-definition-info">
          <span className="definition-label">{activeLabel}</span>
          {/* Removed (Original)/(Custom) indicator as label often includes this */}
        </div>
        <span className="toggle-icon">{isExpanded ? "" : ""}</span>
      </div>

      {isExpanded && (
        <div className="definition-selector-dropdown">
          <div className="dropdown-header">
            <h4>Switch Prime Definition</h4>
            <button
              className="close-dropdown"
              onClick={() => setIsExpanded(false)}
              aria-label="Close definition selector"
            >
              
            </button>
          </div>
          <div className="definitions-list">
            {availableDefinitions.map((definition) => {
              const optionIsOriginal =
                definition.id === "original_data_definition";
              const optionDefaultColor = optionIsOriginal
                ? "#006064"
                : "#ff8f00";
              const optionColor = getColorForDefinition
                ? getColorForDefinition(definition.id)
                : definition.color || optionDefaultColor;

              return (
                <div
                  key={definition.id}
                  className={`definition-option ${
                    definition.id === activeDefinitionKey ? "active" : ""
                  }`}
                  onClick={() => handleDefinitionSelect(definition.id)}
                  style={{ borderLeftColor: optionColor }}
                  role="button"
                  tabIndex={0} // Make it focusable
                  onKeyPress={(e) =>
                    e.key === "Enter" && handleDefinitionSelect(definition.id)
                  } // Keyboard accessibility
                >
                  <span
                    className="definition-name"
                    // Optional: set text color to match border
                    // style={{ color: optionColor }}
                  >
                    {definition.label}
                  </span>
                  {definition.id === activeDefinitionKey && (
                    <span className="active-badge">Active</span>
                  )}

                  {/* --- Parameter Display Logic --- */}
                  {/* Only show details if parameters object exists */}
                  {definition.parameters && (
                    <div className="definition-details">
                      {/* Check if BOTH specific parameters exist and are valid numbers */}
                      {definition.parameters.thresholdPct != null &&
                      typeof definition.parameters.thresholdPct === "number" &&
                      definition.parameters.gamesPctThreshold != null &&
                      typeof definition.parameters.gamesPctThreshold ===
                        "number" ? (
                        <>
                          {/* Display T% and G% */}
                          <span>T: {definition.parameters.thresholdPct}%</span>
                          <span>
                            G: {definition.parameters.gamesPctThreshold}%
                          </span>
                        </>
                      ) : // If parameters object exists but values are missing/invalid, render nothing here
                      null}
                    </div>
                  )}
                  {/* --- End Parameter Display Logic --- */}
                </div>
              );
            })}
          </div>
          <div className="dropdown-footer">
            <p>Select a definition to update the dashboard view.</p>
          </div>
        </div>
      )}
    </div>
  );
};

export default DefinitionSelector;
