import React, { useState, useEffect, useRef, useCallback } from "react";
import { leagueColorMapping } from "../utils/constants"; // Assuming you have leagueToSportMapping here too if needed elsewhere
import ContextInfo from "./ContextInfo";
import LeagueDisplay from "./LeagueDisplay";
import SportDisplay from "./SportDisplay";
import _ from "lodash";
import toast, { Toaster } from "react-hot-toast";
import "../styles/PrimeIdentifier.css";

// --- Constants ---
const DEFAULT_THRESHOLD_PCT = 70;
const DEFAULT_GAMES_PCT_THRESHOLD = 100; // Keep if you have a games threshold slider (not shown in provided JSX)
const MAX_CUSTOM_DEFINITIONS = 5;
const ORIGINAL_DEFINITION_ID = "original_data_definition";
const API_ENDPOINT =
  "https://peakperformr-api-final.yellowcliff-d82a2703.westus2.azurecontainerapps.io/recalculate";
const ESTIMATED_API_TIME = 70; // seconds

// --- Utility Functions ---
// Use only thresholdPct if gamesPctThreshold is not used
const getParamsKey = (params) => `t${params.thresholdPct}`;
// const getParamsKey = (params) => `t${params.thresholdPct}_g${params.gamesPctThreshold}`;

const calculateLeaguePrimeStats = (primes) => {
  if (!Array.isArray(primes) || primes.length === 0) return [];
  const leagueGroups = _.groupBy(primes, "league");

  const stats = Object.entries(leagueGroups)
    .map(([league, playersInLeague]) => {
      if (!playersInLeague || playersInLeague.length === 0) return null;

      const validDurations = playersInLeague
        .map((p) => p?.prime_duration)
        .filter((d) => typeof d === "number" && isFinite(d));
      const avgDuration =
        validDurations.length > 0 ? _.mean(validDurations) : 0;

      const validStartAges = playersInLeague
        .map((p) => p?.start_age)
        .filter((a) => typeof a === "number" && isFinite(a));
      const avgStartAge =
        validStartAges.length > 0 ? _.mean(validStartAges) : 0;

      const validEndAges = playersInLeague
        .map((p) => p?.end_age)
        .filter((a) => typeof a === "number" && isFinite(a));
      const avgEndAge = validEndAges.length > 0 ? _.mean(validEndAges) : 0;

      // Get the sport for this league - Ensure sport property exists on player objects
      const sport = playersInLeague.find((p) => p.sport)?.sport || ""; // Find first player with sport defined

      return {
        league,
        sport, // Make sure this is populated correctly from your data processing
        avgDuration,
        avgStartAge,
        avgEndAge,
        playerCount: playersInLeague.length,
        color: leagueColorMapping[league] || "#cccccc", // Keep color for potential use
      };
    })
    .filter(Boolean);

  // Sort primarily by player count descending, then duration descending
  return _.orderBy(stats, ["playerCount", "avgDuration"], ["desc", "desc"]);
};

// --- Main Component ---
const PrimeIdentifier = ({
  originalData,
  availableDefinitions: globalAvailableDefinitions,
  activeDefinitionKey: globalActiveDefinitionKey,
  onPrimeChange,
  onDefinitionSelect,
  getColorForDefinition, // Keep if used for definition comparison coloring
  useLogos = false,
  // toggleLogos = () => {}, // Not used locally
  simplifiedView = false, // Keep if used elsewhere
}) => {
  // Local state for sliders
  const [thresholdPct, setThresholdPct] = useState(DEFAULT_THRESHOLD_PCT);
  // const [gamesPctThreshold, setGamesPctThreshold] = useState(DEFAULT_GAMES_PCT_THRESHOLD); // Uncomment if using games threshold

  // State for API call management
  const [activeApiRequests, setActiveApiRequests] = useState({});
  const [apiErrors, setApiErrors] = useState({});
  const [countdownTimer, setCountdownTimer] = useState(0);
  const countdownIntervalRef = useRef(null);

  // State for the data currently being displayed
  const [displayData, setDisplayData] = useState({
    rawPrimes: [],
    splinePrimes: [],
    pqi: [],
    cqi: [],
    fullData: [],
    leagueStats: [],
    label: "N/A",
  });

  // --- Effects ---
  // Synchronize local view with global state
  useEffect(() => {
    const currentGlobalDef = globalAvailableDefinitions?.find(
      (def) => def.id === globalActiveDefinitionKey
    );

    if (currentGlobalDef) {
      let sourceData;
      if (currentGlobalDef.id === ORIGINAL_DEFINITION_ID) {
        sourceData = originalData;
      } else {
        sourceData = currentGlobalDef.primeData || originalData; // Fallback needed?
      }

      if (
        sourceData &&
        sourceData.primesRawData &&
        sourceData.primesSplineData &&
        sourceData.pqiData &&
        sourceData.cqiData //&& sourceData.fullData // fullData might be optional now
      ) {
        // Recalculate league stats based on the selected definition's data
        const currentLeagueStats = calculateLeaguePrimeStats(
          sourceData.primesSplineData || []
        );

        setDisplayData({
          rawPrimes: sourceData.primesRawData || [],
          splinePrimes: sourceData.primesSplineData || [],
          pqi: sourceData.pqiData || [],
          cqi: sourceData.cqiData || [],
          fullData: sourceData.fullData || [], // May be empty/null if not sent by API
          leagueStats: currentLeagueStats,
          label: currentGlobalDef.label || "Unnamed Definition",
        });

        // Update slider based on selected definition's parameters
        if (
          currentGlobalDef.parameters &&
          currentGlobalDef.id !== ORIGINAL_DEFINITION_ID
        ) {
          setThresholdPct(
            currentGlobalDef.parameters.thresholdPct ?? DEFAULT_THRESHOLD_PCT
          );
          // setGamesPctThreshold(currentGlobalDef.parameters.gamesPctThreshold ?? DEFAULT_GAMES_PCT_THRESHOLD); // If using
        } else if (currentGlobalDef.id === ORIGINAL_DEFINITION_ID) {
          // Reset sliders to default for original data
          setThresholdPct(DEFAULT_THRESHOLD_PCT);
          // setGamesPctThreshold(DEFAULT_GAMES_PCT_THRESHOLD); // If using
        }
      } else {
        // Handle cases where data might be missing/invalid for the definition
        console.warn(
          "Invalid or missing source data for definition:",
          globalActiveDefinitionKey,
          sourceData
        );
        setDisplayData({
          rawPrimes: [],
          splinePrimes: [],
          pqi: [],
          cqi: [],
          fullData: [],
          leagueStats: [],
          label: "Error: Invalid Data",
        });
      }
    } else {
      // Handle case where no definition is selected or found
      setDisplayData({
        rawPrimes: [],
        splinePrimes: [],
        pqi: [],
        cqi: [],
        fullData: [],
        leagueStats: [],
        label: "N/A",
      });
    }
  }, [globalActiveDefinitionKey, globalAvailableDefinitions, originalData]);

  // Countdown timer cleanup
  useEffect(() => {
    return () => {
      if (countdownIntervalRef.current) {
        clearInterval(countdownIntervalRef.current);
      }
    };
  }, []);

  // --- Event Handlers ---
  const handleParameterChange = (setter) => (event) => {
    setter(parseInt(event.target.value, 10));
  };

  const startCountdown = (seconds) => {
    if (countdownIntervalRef.current)
      clearInterval(countdownIntervalRef.current);
    setCountdownTimer(seconds);
    countdownIntervalRef.current = setInterval(() => {
      setCountdownTimer((prevTime) => {
        if (prevTime <= 1) {
          clearInterval(countdownIntervalRef.current);
          return 0;
        }
        return prevTime - 1;
      });
    }, 1000);
  };

  const handleCalculateRequest = useCallback(async () => {
    // Use only thresholdPct if gamesPctThreshold is not part of params
    const currentParams = { thresholdPct };
    // const currentParams = { thresholdPct, gamesPctThreshold }; // If using
    const paramsKey = getParamsKey(currentParams);

    if (activeApiRequests[paramsKey]) return; // Already requesting

    const existingGlobalDef = globalAvailableDefinitions?.find(
      (def) => def.id === paramsKey
    );
    if (existingGlobalDef) {
      toast.success(
        `Definition exists: ${existingGlobalDef.label}. Switching view.`,
        { duration: 4000 }
      );
      if (onDefinitionSelect) onDefinitionSelect(paramsKey);
      return;
    }

    const customDefCount =
      globalAvailableDefinitions?.filter(
        (def) => def.id !== ORIGINAL_DEFINITION_ID
      ).length || 0;
    if (customDefCount >= MAX_CUSTOM_DEFINITIONS) {
      toast.error(
        `Maximum of ${MAX_CUSTOM_DEFINITIONS} custom definitions reached.`
      );
      return;
    }

    setActiveApiRequests((prev) => ({ ...prev, [paramsKey]: "loading" }));
    setApiErrors((prev) => {
      const n = { ...prev };
      delete n[paramsKey];
      return n;
    });

    const toastId = toast.loading(`Calculating: T=${thresholdPct}%...`);
    startCountdown(ESTIMATED_API_TIME);

    try {
      const response = await fetch(API_ENDPOINT, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(currentParams), // Send only relevant params
      });

      if (!response.ok) {
        const errorText = await response.text();
        throw new Error(
          `API Error (${response.status}). ${errorText.substring(0, 100)}`
        ); // Limit error text length
      }

      const results = await response.json();

      if (results?.success && results?.parameters) {
        const fullDataProvided =
          Array.isArray(results.fullData) && results.fullData.length > 0;

        // Construct label based on actual parameters returned
        const newLabel = `Custom: T=${results.parameters.thresholdPct}%`;
        // const newLabel = `Custom: T=${results.parameters.thresholdPct}% / G=${results.parameters.gamesPctThreshold}%`; // If using games

        const newDefinitionData = {
          definitionKey: paramsKey, // Use the key generated from input params
          definitionLabel: newLabel,
          definitionParams: results.parameters, // Store returned parameters
          raw: results.rawPrimes || [],
          spline: results.splinePrimes || [],
          pqi: results.pqi || [],
          cqi: results.cqi || [],
          fullData: fullDataProvided ? results.fullData : null,
          isOriginal: false,
        };

        toast.success(`Calculation complete for ${newLabel}`, { id: toastId });
        if (onPrimeChange) onPrimeChange(newDefinitionData);

        clearInterval(countdownIntervalRef.current);
        setCountdownTimer(0);
      } else {
        throw new Error(
          results?.message ||
            "API did not return successful data or parameters."
        );
      }
    } catch (error) {
      console.error(`API Call failed for ${paramsKey}:`, error);
      setApiErrors((prev) => ({ ...prev, [paramsKey]: error.message }));
      toast.error(`Calculation failed: ${error.message}`, {
        id: toastId,
        duration: 6000,
      });
      clearInterval(countdownIntervalRef.current);
      setCountdownTimer(0);
    } finally {
      setActiveApiRequests((prev) => {
        const n = { ...prev };
        delete n[paramsKey];
        return n;
      });
    }
  }, [
    thresholdPct,
    // gamesPctThreshold, // If using
    activeApiRequests,
    globalAvailableDefinitions,
    onPrimeChange,
    onDefinitionSelect,
  ]);

  const handleSelectDefinition = (event) => {
    const selectedKey = event.target.value;
    if (selectedKey !== globalActiveDefinitionKey && onDefinitionSelect) {
      onDefinitionSelect(selectedKey);
      setApiErrors((prev) => {
        const n = { ...prev };
        delete n[selectedKey];
        return n;
      });
    }
  };

  // --- Derived State for Rendering ---
  const isLoadingAny = Object.values(activeApiRequests).includes("loading");
  const customDefCount =
    globalAvailableDefinitions?.filter(
      (def) => def.id !== ORIGINAL_DEFINITION_ID
    ).length || 0;
  const canCalculate = !isLoadingAny && customDefCount < MAX_CUSTOM_DEFINITIONS;
  // Use only thresholdPct if gamesPctThreshold is not used
  const currentParamsKey = getParamsKey({ thresholdPct });
  // const currentParamsKey = getParamsKey({ thresholdPct, gamesPctThreshold }); // If using
  const isCurrentParamLoading =
    activeApiRequests[currentParamsKey] === "loading";
  const isCurrentParamAvailable = !!globalAvailableDefinitions?.some(
    (def) => def.id === currentParamsKey
  );

  // Adjust tooltip based on active parameters
  const calculateButtonTooltip = isCurrentParamLoading
    ? `Calculating... (${countdownTimer}s remaining)`
    : isCurrentParamAvailable
    ? `Definition with T=${thresholdPct}% already exists.`
    : customDefCount >= MAX_CUSTOM_DEFINITIONS
    ? `Maximum of ${MAX_CUSTOM_DEFINITIONS} custom definitions reached.`
    : `Calculate new data with T=${thresholdPct}%. (Est. ${ESTIMATED_API_TIME}s)`;
  // : `Calculate new data with T=${thresholdPct}% / G=${gamesPctThreshold}%. (Est. ${ESTIMATED_API_TIME}s)`; // If using games

  const definitionOptions =
    globalAvailableDefinitions
      ?.sort((a, b) =>
        a.id === ORIGINAL_DEFINITION_ID
          ? -1
          : b.id === ORIGINAL_DEFINITION_ID
          ? 1
          : 0
      )
      .map((def) => ({
        value: def.id,
        label: `${def.label}${
          activeApiRequests[def.id] ? " (Calculating...)" : ""
        }${apiErrors[def.id] ? " (Error!)" : ""}`,
        error: apiErrors[def.id] || null,
      })) || [];

  // Stats based on currently displayed data (using spline primes)
  const avgSplineDuration = (
    displayData.splinePrimes.reduce(
      (sum, p) => sum + (Number(p.prime_duration) || 0), // Ensure conversion to number
      0
    ) / (displayData.splinePrimes.length || 1)
  ) // Avoid division by zero
    .toFixed(1);
  const splineCount = displayData.splinePrimes.length;

  return (
    <div className="prime-identifier component-box">
      <Toaster position="top-right" containerStyle={{ zIndex: 9999 }} />
      <div className="section-header">
        <h2>
          Prime Definition Explorer
          <ContextInfo
            title="Prime Definition Explorer"
            description="This tool lets you experiment with different definitions of 'prime years' by adjusting the performance threshold."
          />
        </h2>
        {/* Moved status indicator next to title for better flow */}
        {displayData.label !== "N/A" && (
          <span
            className={`status-indicator ${
              globalActiveDefinitionKey === ORIGINAL_DEFINITION_ID
                ? "original"
                : "modified"
            }`}
          >
            Viewing: {displayData.label}
          </span>
        )}
        {apiErrors[globalActiveDefinitionKey] && (
          <div className="api-error-message inline-error">
            Error loading this definition:{" "}
            {apiErrors[globalActiveDefinitionKey]}
          </div>
        )}
      </div>

      {/* Settings Panel */}
      <div className="settings-panel">
        {/* Threshold Slider */}
        <div className="threshold-controls control-group">
          {" "}
          {/* Use the class for width */}
          <div className="slider-container">
            {" "}
            {/* Add wrapper if needed */}
            <label htmlFor="threshold-pct">
              Performance Threshold (%) : {thresholdPct}%
              <ContextInfo
                title="Performance Threshold"
                description="The percentage of peak performance an athlete must maintain to be considered in their prime years."
              />
            </label>
            {/* Container for Slider and Labels */}
            <div className="slider-with-labels">
              <div className="slider-label left green">
                More Flexible Prime <span className="arrow"></span>
              </div>
              <input
                type="range"
                id="threshold-pct"
                min="0"
                max="100"
                step="5"
                value={thresholdPct}
                onChange={handleParameterChange(setThresholdPct)}
                disabled={isLoadingAny}
              />
              <div className="slider-label right red">
                <span className="arrow"></span> Stricter Prime
              </div>
            </div>
          </div>
          {/* Uncomment if adding Games Played Threshold
           <div className="slider-container">
             <label htmlFor="games-pct-threshold">Games Played Threshold (%)</label>
             <input type="range" id="games-pct-threshold" min="0" max="100" step="10"
                    value={gamesPctThreshold} onChange={handleParameterChange(setGamesPctThreshold)} disabled={isLoadingAny} />
             <span>{gamesPctThreshold}%</span>
           </div>
           */}
        </div>

        {/* Definition Management */}
        <div className="definition-management">
          <div className="control-group definition-selector">
            <label htmlFor="definition-select">View Definition:</label>
            <select
              id="definition-select"
              value={globalActiveDefinitionKey}
              onChange={handleSelectDefinition}
              disabled={isLoadingAny}
            >
              {definitionOptions.map((opt) => (
                <option
                  key={opt.value}
                  value={opt.value}
                  title={opt.error || ""}
                >
                  {opt.label}
                </option>
              ))}
            </select>
            {apiErrors[globalActiveDefinitionKey] && (
              <span className="error-text"> (Load Error!)</span>
            )}
          </div>

          <div className="action-buttons">
            <button
              onClick={handleCalculateRequest}
              disabled={
                !canCalculate ||
                isCurrentParamLoading ||
                isCurrentParamAvailable
              }
              className={`calculate-button ${
                isCurrentParamLoading ? "loading" : ""
              }`}
              title={calculateButtonTooltip}
            >
              {isCurrentParamLoading
                ? `Calculating... (${countdownTimer}s)`
                : "Calculate & Add"}
            </button>
          </div>
        </div>
      </div>

      {/* Global Stats */}
      <div className="global-stats-summary component-box">
        <h4>Overall Stats (Viewing: {displayData.label})</h4>
        <div className="stat-cards">
          <div className="stat-card">
            Players (Spline Primes): <span>{splineCount.toLocaleString()}</span>
          </div>
          <div className="stat-card">
            Avg. Prime Duration (Spline): <span>{avgSplineDuration} yrs</span>
          </div>
        </div>
      </div>

      {/* League Comparison Table */}
      {displayData.leagueStats.length > 0 && (
        <div className="league-prime-comparison component-box">
          <h4>
            League Comparison{" "}
            <ContextInfo
              title="League Prime Comparison"
              description="This table compares prime statistics across different leagues using the current definition."
            />
          </h4>
          <table>
            <thead>
              <tr>
                <th>League</th>
                <th>Players</th>
                <th>Avg Start Age</th>
                <th>Avg End Age</th>
                <th>Avg Duration</th>
              </tr>
            </thead>
            <tbody>
              {displayData.leagueStats.map((lg) => (
                <tr key={lg.league}>
                  {/* League Column */}
                  <td>
                    <LeagueDisplay
                      league={lg.league}
                      useLogos={useLogos}
                      size="large"
                      bold={true}
                      // Use colored text when logos are off for consistency
                      showBackground={!useLogos}
                    />
                  </td>

                  <td>{lg.playerCount.toLocaleString()}</td>
                  <td>{lg.avgStartAge?.toFixed(1)}</td>
                  <td>{lg.avgEndAge?.toFixed(1)}</td>
                  <td>{lg.avgDuration?.toFixed(1)}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}

      {/* Cross-Definition Comparison Table */}
      {globalAvailableDefinitions && globalAvailableDefinitions.length > 1 && (
        <div className="definitions-comparison component-box">
          <h4>
            Cross-Definition Comparison{" "}
            <ContextInfo
              title="Definition Comparison"
              description="This table compares statistics across different prime definitions."
            />
          </h4>
          <table className="comparison-table">
            <thead>
              <tr>
                <th>Definition</th>
                <th>Players</th>
                <th>Avg Start Age</th>
                <th>Avg End Age</th>
                <th>Avg Duration</th>
                <th>Actions</th>
              </tr>
            </thead>
            <tbody>
              {globalAvailableDefinitions.map((def) => {
                // Determine data source for this definition
                const dataSource =
                  def.id === ORIGINAL_DEFINITION_ID
                    ? originalData
                    : def.primeData;
                const primesToAnalyze = dataSource?.primesSplineData || [];

                // Calculate *overall* stats for this definition
                const totalPlayers = primesToAnalyze.length;
                const avgStart =
                  totalPlayers > 0
                    ? _.meanBy(
                        primesToAnalyze.filter((p) => p.start_age != null),
                        "start_age"
                      ).toFixed(1)
                    : "N/A";
                const avgEnd =
                  totalPlayers > 0
                    ? _.meanBy(
                        primesToAnalyze.filter((p) => p.end_age != null),
                        "end_age"
                      ).toFixed(1)
                    : "N/A";
                const avgDur =
                  totalPlayers > 0
                    ? _.meanBy(
                        primesToAnalyze.filter((p) => p.prime_duration != null),
                        "prime_duration"
                      ).toFixed(1)
                    : "N/A";

                return (
                  <tr
                    key={def.id}
                    className={
                      def.id === globalActiveDefinitionKey ? "active-row" : ""
                    }
                  >
                    <td>{def.label}</td>
                    <td>{totalPlayers.toLocaleString() || "N/A"}</td>
                    <td>{avgStart}</td>
                    <td>{avgEnd}</td>
                    <td>{avgDur}</td>
                    <td>
                      <button
                        onClick={() =>
                          onDefinitionSelect && onDefinitionSelect(def.id)
                        }
                        className="view-button"
                        disabled={
                          def.id === globalActiveDefinitionKey ||
                          !!activeApiRequests[def.id]
                        }
                      >
                        View
                      </button>
                      {/* Optional: Add delete button here */}
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
      )}
    </div>
  );
};

export default PrimeIdentifier;
