// src/App.js
import React, { useState, useEffect } from "react";
// --- Router Imports ---
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Navigate,
} from "react-router-dom";

// --- Component Imports ---
import Dashboard from "./components/Dashboard";
import PosterDisplay from "./components/PosterDisplay";
import LandingPage from "./components/LandingPage"; // <-- Re-import LandingPage
import DataErrorBoundary from "./components/DataErrorBoundary";

// --- Utils & Styles ---
import { loadAllData } from "./utils/dataLoader";
import "./styles/App.css";

function App() {
  // --- State remains the same ---
  const [data, setData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [loadingStage, setLoadingStage] = useState("Initializing...");

  // --- Data Loading useEffect remains the same ---
  useEffect(() => {
    const fetchData = async () => {
      try {
        setLoadingStage("Preparing to load data...");
        console.log("App is attempting to load data...");
        setLoadingStage("Loading data...");
        const loadedData = await loadAllData();
        console.log("Data loaded successfully in App.js");
        setData(loadedData);
        setLoading(false);
      } catch (err) {
        console.error("Error loading data in App.js:", err);
        setError(`${err.message}`);
        setLoading(false);
      }
    };
    fetchData();
  }, []);

  // --- Loading State Display ---
  if (loading) {
    return (
      <div className="loading-container">
        <div className="loading-stage">{loadingStage}</div>
        <div className="loading-message">This should only take a moment.</div>
      </div>
    );
  }

  // --- Error State Display ---
  if (error) {
    return (
      <div className="error-container">
        <div className="error-title">Error Loading Data</div>
        <div className="error">{error}</div>
        <div className="troubleshooting">
          {/* ... troubleshooting steps ... */}
          <h3>Troubleshooting Steps:</h3>
          <ol>
            <li>Verify that your CSV files exist in the public/data folder</li>
            <li>
              Check that your CSV files have the correct structure and headers
            </li>
            <li>Try refreshing the page</li>
            <li>Restart your development server (npm start)</li>
            <li>
              Check your browser's console (F12) for more detailed error
              messages
            </li>
          </ol>
          <button
            onClick={() => window.location.reload()}
            className="reload-button"
          >
            Reload Page
          </button>
        </div>
      </div>
    );
  }

  // --- Success State: Render Routed Application ---
  return (
    // Router setup - Basename might not be needed for dev server if PUBLIC_URL works
    // <Router basename={process.env.PUBLIC_URL}>
    <Router>
      {" "}
      {/* Simplest for dev */}
      <div className="App">
        <Routes>
          {/* Route 1: Render LandingPage at the root */}
          <Route path="/" element={<LandingPage />} />

          {/* Route 2: Dashboard view */}
          <Route
            path="/dashboard"
            element={
              <DataErrorBoundary>
                {/* Pass data only when rendering Dashboard */}
                <Dashboard data={data} />
              </DataErrorBoundary>
            }
          />

          {/* Route 3: Poster Display */}
          <Route path="/poster" element={<PosterDisplay />} />

          {/* Optional: Catch-all for unknown paths, redirect to landing page */}
          <Route path="*" element={<Navigate to="/" replace />} />
        </Routes>
      </div>
    </Router>
  );
}

export default App;
