import {
  ResponsiveContainer,
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  PieChart,
  Pie,
  Cell,
  BarChart,
  Bar,
  // ReferenceLine, // Potentially unused now if LeagueAgingVisual was the only user
  Text,
} from "recharts";
import { getAgingCurvesByLeague } from "../utils/dataProcessing";
import { leagueColorMapping } from "../utils/constants";
import EnhancedFilters from "./EnhancedFilters";
import ContextInfo from "./ContextInfo";
import LeagueDisplay from "./LeagueDisplay";
import SportDisplay from "./SportDisplay"; // Keep if used elsewhere, like in Legends
