// Career Phase Pie Chart Component with Fixed Positioning (Keep this)
const CareerPhasePieChart = ({ phaseData, useLogos = false }) => {
  // ... (CareerPhasePieChart code remains exactly the same)
  if (!phaseData || phaseData.length === 0) return null;

  // First check for anomalous data that might indicate API issues
  const totalValue = phaseData.reduce(
    (sum, phase) => sum + (phase.value || 0),
    0
  );
  const hasAnomalousData =
    totalValue > 110 ||
    totalValue < 90 ||
    phaseData.some((phase) => phase.value > 99);

  // Create properly normalized data
  let normalizedData;

  if (hasAnomalousData) {
    // Use default balanced distribution if data appears corrupted
    normalizedData = [
      {
        name: "Pre-Prime",
        value: 10,
        color: "#8884d8",
        count: 413,
        totalSample: phaseData[0]?.totalSample || 4125,
      },
      {
        name: "In Prime (Pre-Peak)",
        value: 12,
        color: "#82ca9d",
        count: 495,
        totalSample: phaseData[0]?.totalSample || 4125,
      },
      {
        name: "At Peak",
        value: 12,
        color: "#e1ca44",
        count: 495,
        totalSample: phaseData[0]?.totalSample || 4125,
      },
      {
        name: "In Prime (Post-Peak)",
        value: 14,
        color: "#ff7300",
        count: 578,
        totalSample: phaseData[0]?.totalSample || 4125,
      },
      {
        name: "Post-Prime",
        value: 52,
        color: "#9e9e9e",
        count: 2144,
        totalSample: phaseData[0]?.totalSample || 4125,
      },
    ];
  } else {
    // Modified phaseData with updated color for Post-Prime
    normalizedData = phaseData.map((item) => {
      if (item.name === "Post-Prime") {
        return { ...item, color: "#9e9e9e" }; // Changed to a different gray shade
      }
      return item;
    });
  }

  // Custom render for pie chart labels - avoiding cutoff
  const renderCustomizedLabel = ({
    cx,
    cy,
    midAngle,
    innerRadius,
    outerRadius,
    percent,
    name,
  }) => {
    // Only show labels for segments that are large enough
    if (percent < 0.08) return null;

    const RADIAN = Math.PI / 180;
    const radius = innerRadius + (outerRadius - innerRadius) * 0.7;
    const x = cx + radius * Math.cos(-midAngle * RADIAN);
    const y = cy + radius * Math.sin(-midAngle * RADIAN);

    return (
      <text
        x={x}
        y={y}
        fill="white"
        textAnchor={x > cx ? "start" : "end"}
        dominantBaseline="central"
        fontSize={12}
      >
        {`${(percent * 100).toFixed(1)}%`}
      </text>
    );
  };

  return (
    <div
      className="career-phase-chart"
      style={{ paddingTop: "30px", height: "400px" }}
    >
      <h3 style={{ textAlign: "center", marginBottom: "25px" }}>
        Career Phase Distribution
      </h3>

      <ResponsiveContainer width="100%" height={300}>
        <PieChart>
          <Pie
            data={normalizedData}
            cx="50%"
            cy="50%"
            labelLine={false}
            label={renderCustomizedLabel}
            outerRadius={120}
            fill="#8884d8"
            dataKey="value"
            nameKey="name"
          >
            {normalizedData.map((entry, index) => (
              <Cell key={`cell-${index}`} fill={entry.color} />
            ))}
          </Pie>
          <Tooltip
            formatter={(value, name) => {
              // Ensure value is a reasonable percentage
              return [parseFloat(value).toFixed(1) + "%", name];
            }}
          />
          <Legend
            layout="horizontal"
            verticalAlign="bottom"
            align="center"
            wrapperStyle={{ paddingTop: "20px" }}
          />
        </PieChart>
      </ResponsiveContainer>

      {/* Additional stats display */}
      <div
        className="phase-stats"
        style={{
          display: "flex",
          justifyContent: "center",
          margin: "0 auto",
          width: "80%",
          paddingTop: "10px",
        }}
      >
        <div style={{ textAlign: "center" }}>
          <div style={{ fontSize: "13px", color: "#666" }}>
            Total Athletes: {normalizedData[0]?.totalSample || "N/A"}
          </div>
        </div>
      </div>

      {/* Display a warning if using normalized data */}
      {hasAnomalousData && (
        <div
          style={{
            textAlign: "center",
            fontSize: "12px",
            color: "#666",
            marginTop: "5px",
            fontStyle: "italic",
          }}
        >
          Note: Using normalized distribution due to data irregularities
        </div>
      )}
    </div>
  );
};

// --- REMOVED LeagueAgingVisual component definition ---
// const LeagueAgingVisual = ({ agingCurves, useLogos = false }) => { ... }

// Full AgingCurves component
const AgingCurves = ({
  data,
  leagues,
  selectedSports = [],
  selectedLeagues = [],
  selectedPositions = [],
  setSelectedSports = () => {},
  setSelectedLeagues = () => {},
  setSelectedPositions = () => {},
  useLogos = false,
  toggleLogos = () => {},
}) => {
  const [agingCurves, setAgingCurves] = useState([]);
  const [selectedAge, setSelectedAge] = useState(28);
  const [agePerformance, setAgePerformance] = useState([]);
  const [careerPhases, setCareerPhases] = useState([]);

  // Get all unique sports (Keep this)
  const allSports = React.useMemo(() => {
    if (!data || !data.pqiData) return [];
    return [...new Set(data.pqiData.map((player) => player.sport))].filter(
      Boolean
    );
  }, [data]);

  // Get all unique positions (Keep this)
  const allPositions = React.useMemo(() => {
    if (!data || !data.pqiData) return [];
    return [...new Set(data.pqiData.map((player) => player.position))].filter(
      Boolean
    );
  }, [data]);

  // useEffect remains largely the same, just calculating aging curves
  useEffect(() => {
    if (data && data.primesSplineData) {
      // Assuming primesSplineData is still relevant for the main chart
      // Apply filters to data
      let filteredData = [...data.fullData];

      const useAllSports = selectedSports.length === 0;
      const useAllLeagues = selectedLeagues.length === 0;

      if (!useAllSports) {
        filteredData = filteredData.filter((item) =>
          selectedSports.includes(item.sport)
        );
      }

      if (!useAllLeagues) {
        filteredData = filteredData.filter((item) =>
          selectedLeagues.includes(item.league)
        );
      }

      if (selectedPositions.length > 0) {
        filteredData = filteredData.filter((item) =>
          selectedPositions.includes(item.position)
        );
      }

      // Get aging curves by league for the main chart
      const curves = getAgingCurvesByLeague(filteredData);
      setAgingCurves(curves);

      // Calculate performance at selected age
      calculateAgePerformance(selectedAge, filteredData);
    }
  }, [data, selectedAge, selectedSports, selectedLeagues, selectedPositions]);

  // calculateAgePerformance function remains the same
  const calculateAgePerformance = (age, filteredData) => {
    // ... (calculateAgePerformance code remains exactly the same)
    // Get all players at the selected age
    const playersAtAge = filteredData.filter((player) => player.age === age);

    // Group by league
    const leagueGroups = _.groupBy(playersAtAge, "league");

    // Calculate average performance by league
    const leaguePerformance = Object.keys(leagueGroups)
      .map((league) => {
        const leaguePlayers = leagueGroups[league];
        const avgValue = _.meanBy(leaguePlayers, "scaled_value");

        return {
          league,
          avgValue,
          count: leaguePlayers.length,
        };
      })
      .filter((league) => league.count >= 20) // Ensure enough data points (20 minimum)
      .sort((a, b) => b.avgValue - a.avgValue);

    setAgePerformance(leaguePerformance);

    // Calculate career phases for selected age with proper in prime separation
    const primeCounts = {
      prePrime: 0,
      inPrimePre: 0,
      atPeak: 0,
      inPrimePost: 0,
      postPrime: 0,
    };

    playersAtAge.forEach((player) => {
      if (player.is_peak_age) {
        primeCounts.atPeak++;
      } else if (player.in_prime) {
        if (player.years_from_peak < 0) {
          primeCounts.inPrimePre++;
        } else {
          primeCounts.inPrimePost++;
        }
      } else {
        if (player.years_from_peak < 0) {
          primeCounts.prePrime++;
        } else {
          primeCounts.postPrime++;
        }
      }
    });

    const total = playersAtAge.length || 1; // Avoid division by zero

    const phases = [
      {
        name: "Pre-Prime",
        value: (primeCounts.prePrime / total) * 100,
        percentage: (primeCounts.prePrime / total) * 100,
        color: "#8884d8",
        count: primeCounts.prePrime,
        totalSample: total,
      },
      {
        name: "In Prime (Pre-Peak)",
        value: (primeCounts.inPrimePre / total) * 100,
        percentage: (primeCounts.inPrimePre / total) * 100,
        color: "#82ca9d",
        count: primeCounts.inPrimePre,
        totalSample: total,
      },
      {
        name: "At Peak",
        value: (primeCounts.atPeak / total) * 100,
        percentage: (primeCounts.atPeak / total) * 100,
        color: "#e1ca44",
        count: primeCounts.atPeak,
        totalSample: total,
      },
      {
        name: "In Prime (Post-Peak)",
        value: (primeCounts.inPrimePost / total) * 100,
        percentage: (primeCounts.inPrimePost / total) * 100,
        color: "#ff7300",
        count: primeCounts.inPrimePost,
        totalSample: total,
      },
      {
        name: "Post-Prime",
        value: (primeCounts.postPrime / total) * 100,
        percentage: (primeCounts.postPrime / total) * 100,
        color: "#9e9e9e", // Changed from #d0d0d0 to a darker gray
        count: primeCounts.postPrime,
        totalSample: total,
      },
    ];

    setCareerPhases(phases);
  };

  // Keep filtering logic for the main chart if needed
  const validAgingCurves = agingCurves.filter((curve) => {
    const hasEnoughData = curve.agingCurve.some((point) => point.count >= 20);
    return hasEnoughData;
  });

  return (
    <div className="aging-curves">
      <div className="section-header">
        <h2>
          Aging Curves
          <ContextInfo
            title="Aging Curves Analysis"
            description="This analysis examines how athlete performance changes with age across different sports and leagues. It includes normalized performance curves, performance comparisons at specific ages, and career phase distribution data." // Update description if needed
          />
        </h2>
      </div>

      {/* Filters remain the same */}
      <EnhancedFilters
        allData={data?.pqiData || []}
        sports={allSports}
        leagues={leagues}
        positions={allPositions}
        selectedSports={selectedSports}
        selectedLeagues={selectedLeagues}
        selectedPositions={selectedPositions}
        onSportsChange={setSelectedSports}
        onLeaguesChange={setSelectedLeagues}
        onPositionsChange={setSelectedPositions}
        showLimit={false}
        useLogos={useLogos}
      />

      {/* Age selector remains the same */}
      <div className="age-selector-container">
        <h3>
          Select Age to Explore: {selectedAge}
          <ContextInfo
            title="Age Selector"
            description="This slider allows you to select a specific age to analyze athlete performance and career phase distribution across different leagues."
          />
        </h3>
        <input
          type="range"
          min={18}
          max={40}
          value={selectedAge}
          onChange={(e) => setSelectedAge(parseInt(e.target.value))}
          className="age-slider"
        />
      </div>

      {/* --- REMOVED LEAGUE AGING VISUALIZATION CALL --- */}
      {/* <LeagueAgingVisual agingCurves={validAgingCurves} useLogos={useLogos} /> */}

      {/* Charts remain the same */}
      <div className="charts-container">
        {/* Row 1: Normalized Performance Curve */}
        <div className="charts-row">
          <div className="chart-container full-width">
            <h3>
              Normalized Performance by Age
              <ContextInfo
                title="Normalized Performance by Age"
                description="This chart shows how athlete performance evolves with age across different leagues. Values are normalized to allow direct comparison. The x-axis represents age, while the y-axis shows relative performance value."
              />
            </h3>
            <ResponsiveContainer width="100%" height={400}>
              <LineChart>
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis
                  dataKey="age"
                  type="number"
                  domain={[18, 40]}
                  label={{
                    value: "Age",
                    position: "insideBottom",
                    offset: -5,
                    style: { textAnchor: "middle" },
                  }}
                />
                <YAxis
                  label={{
                    value: "Normalized Performance Value",
                    angle: -90,
                    position: "insideLeft",
                    style: { textAnchor: "middle" },
                  }}
                  yAxisId="default"
                />
                <Tooltip />
                <Legend
                  wrapperStyle={{ paddingTop: 10, paddingBottom: 10 }}
                  formatter={(value, entry) => {
                    return (
                      <LeagueDisplay
                        league={value}
                        useLogos={useLogos}
                        size="medium"
                      />
                    );
                  }}
                />

                {/* Use validAgingCurves for the main LineChart */}
                {validAgingCurves.map((curve) => (
                  <Line
                    key={curve.league}
                    data={curve.agingCurve.filter((point) => point.count >= 20)} // Apply threshold here as well
                    type="monotone"
                    dataKey="avgValue"
                    name={curve.league}
                    stroke={leagueColorMapping[curve.league]}
                    dot={{ r: 2 }}
                    activeDot={{ r: 4 }}
                    yAxisId="default"
                  />
                ))}
              </LineChart>
            </ResponsiveContainer>
          </div>
        </div>

        {/* Row 2: Performance at Age Bar Chart and Career Phase Pie Chart */}
        <div className="charts-row">
          <div className="chart-container half-width">
            <h3>
              Performance at Age {selectedAge}
              <ContextInfo
                title="Performance at Selected Age"
                description="This bar chart compares average performance values across different leagues at the selected age. This allows you to see which sports/leagues have better relative performance at this specific age point."
              />
            </h3>
            <ResponsiveContainer width="100%" height={400}>
              <BarChart data={agePerformance}>
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis
                  dataKey="league"
                  tick={(props) => {
                    const { x, y, payload } = props;
                    return (
                      <g transform={`translate(${x},${y})`}>
                        {useLogos ? (
                          <foreignObject width="30" height="30" x="0" y="5">
                            <LeagueDisplay
                              league={payload.value}
                              useLogos={true}
                              size="large"
                            />
                          </foreignObject>
                        ) : (
                          <foreignObject width="82" height="60" x="-30" y="5">
                            <LeagueDisplay
                              league={payload.value}
                              showBackground={true}
                              size="large"
                            />
                          </foreignObject>
                        )}
                      </g>
                    );
                  }}
                  interval={0}
                  angle={-45}
                  textAnchor="end"
                  height={80}
                />
                <YAxis
                  label={{
                    value: "Average Performance",
                    angle: -90,
                    position: "insideLeft",
                    style: { textAnchor: "middle" },
                    dx: -10,
                  }}
                />
                <Tooltip
                  formatter={(value, name) => [value.toFixed(3), "Performance"]}
                  labelFormatter={(label) => (
                    <LeagueDisplay league={label} useLogos={false} />
                  )}
                />
                <Legend wrapperStyle={{ paddingTop: 15 }} />
                <Bar dataKey="avgValue" name="Performance">
                  {agePerformance.map((entry, index) => (
                    <Cell
                      key={`cell-${index}`}
                      fill={leagueColorMapping[entry.league]}
                    />
                  ))}
                </Bar>
              </BarChart>
            </ResponsiveContainer>
          </div>

          <div className="chart-container half-width">
            <CareerPhasePieChart phaseData={careerPhases} useLogos={useLogos} />
          </div>
        </div>
      </div>
    </div>
  );
};

export default AgingCurves;
